<template >
<div  @contextmenu="contextMenu($event)" class="container" >
 <v-speed-dial  v-show="isDesktop" direction="bottom" v-model="fab" top right absolute>
        <template #activator>
         <v-btn color="purple darken-4" dark outlined fab>
          <v-icon>mdi-pencil</v-icon>
        </v-btn>
        </template>
     
        <v-btn fab small color="purple darken-3" @click="showEdit()"> <v-icon color="white">mdi-pencil</v-icon></v-btn>
        <v-btn fab small  color="purple darken-3" @click="setEdit(false)"><v-icon color="white">mdi-delete</v-icon></v-btn> 
         <v-btn fab small  color="purple darken-3"  @click="download()" ><a :href="url" :download="filename" target="_blank"><v-icon color="white">mdi-download</v-icon></a></v-btn> 
      
</v-speed-dial>

      <v-btn @click="navigateLeft()" v-show="showNavigateLeft"  plain color="purple darken-4" fab large bottom absolute left class="arrow" >
        <v-icon>mdi-arrow-left</v-icon>
   </v-btn>
   <v-btn  @click="navigateRight()" v-show="showNavigateRight" plain color="purple darken-4" fab large bottom absolute right class="arrow">
        <v-icon>mdi-arrow-right</v-icon>
    </v-btn>
   <v-btn  v-show="isLastPage" href="https://wiccana.github.io/story/index.html" color="purple darken-4" fab large  bottom absolute right class="back">
      <v-icon color="white"> mdi-undo</v-icon>
    </v-btn>
  <v-row align="center" justify="center" class="d-flex flex-row align-center justify-center" height="r">

   <v-img  v-show="!editing" :src="getSourcePage"></v-img>
    <!-- crossorigin="anonymous" -->
      <img v-show="editing" style="max-width:80%;max-height:80%;" ref="imgRef" :src="getEditSourcePage"   /> 
   </v-row>

  
     
 </div>
</template>

<script>
import * as markerjs2 from 'markerjs2';
export default {
  name: 'Slides',
   data () {
     return {
        fab: false,
        url: '',
       pageNumber: 1,
       editing: false
       }
    },
    computed: {
      filename: function() {
        return "HduH_pagina_"+this.pageNumber
      },
    getSourcePage: function () {
      if (this.pageNumber < 10){
        return "./pages/0${number}.jpg".replace("${number}", this.pageNumber)
      }else{
        return "./pages/${number}.jpg".replace("${number}", this.pageNumber)
      }
    },
    getEditSourcePage: function () {
     
      if (this.pageNumber < 10){
        return "./edit/0${number}.jpg".replace("${number}", this.pageNumber)
      }else{
        return "./edit/${number}.jpg".replace("${number}", this.pageNumber)
      }
    },
    isDesktop: function() {
      // const vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
      // const vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0)
      // if (vw > 1200 && vh > 400){
        return true
      // }
      // return false
    },
    showNavigateRight: function () {
      if (this.pageNumber < 65){
        return true

      }
      return false
    },
    showNavigateLeft: function () {
      if (this.pageNumber > 1){
        return true
      }
      return false
    },
    isLastPage: function () {
      if (this.pageNumber == 65){
        return true
      }
      return false
    }
  },
   methods:{
      showMarkerArea() {
        // create a marker.js MarkerArea
        const markerArea = new markerjs2.MarkerArea(this.$refs.imgRef);
        this.$refs.imgRef.crossOrigin = "anonymous"
        markerArea.addRenderEventListener((imgURL) => (this.$refs.imgRef.src = imgURL));
        markerArea.show();  
        setTimeout(() => {  window.dispatchEvent(new Event('resize')); }, 50);
      },
      showEdit(){ 
        this.setEdit(true)
        this.showMarkerArea()
      },
      setEdit(value){
        this.editing = value;
      },
      navigateLeft(){
        this.editing = false
        this.pageNumber = this.pageNumber - 1;
      },
      navigateRight(){
        this.editing = false
        this.pageNumber = this.pageNumber + 1;
      },
     download(){
      this.url = this.$refs.imgRef.src
     }
    }
   

}
</script>

